import styled from "styled-components";
import Subtitle from "./Subtitle";
import Button from "./Button";
import YoutubeIframe from "./YoutubeIframe";
import { useState, useEffect } from "react";
import CallApi from "../API/CallApi";
import { getAll, getItemById, createItem, updateItem } from "../API/Api";
import { RecordingButton, NextButton, PreviousButton } from "./RecordingButtons"

const Container = styled.div`
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: -20px 0 0 0;
  font-weight: 600;
  @media screen and (max-width: 580px) {
    margin: 0;
  }
`;
const YTSize = styled.div`
  width: 100%;
  max-width: 400px;
  aspect-ratio: 4/3;
`;
const Video = styled.div`
  width: 100%;
  max-width: 400px;
  aspect-ratio: 4/3;
  background-size: cover;
  box-shadow: 0 0 5px gray;
  cursor: not-allowed;
`;
const StageButtonContainer = styled.div`
display: flex;
justify-content: center;
align-items: center;
position: absolute;
top: 0;
width: 100%;
@media screen and (max-width: 580px) {
  button {
    display: none;
  }
}
`
const ButtonsContainer = styled.div`
display: flex;
justify-content: center;
align-items: center;
position: fixed;
bottom: 10px;
right: 10px;
z-index: 1;
`
function Body(props) {
  const { currentProject, setCurrentProject } = props;
  const [player, setPlayer] = useState(null);
  const [order, setOrder] = useState(0);

  const [states, setStates] = useState("");
  let videoId;

  if (currentProject) {
    videoId = currentProject.learnResourceLink.match(/\w+:\/\/[^/:]+[^# ]*v=([\w\-]+)/)[1];
  } else {
    videoId = JSON.parse(
      window.localStorage.getItem("currentProject")
    ).learnResourceLink.match(/\w+:\/\/[^/:]+[^# ]*v=([\w\-]+)/)[1];
  }
  useEffect(() => {
    if (currentProject) {

    

      // let proj = getItemById("eachClassLearnProject/project", window.localStorage.getItem("currentProject_id"))
      //   .then((response) => {
      //     setCurrentProject(response);
      //     window.localStorage.setItem("currentProject_id",response.id)
      //     window.localStorage.setItem("currentProject", JSON.stringify(response));
      //   })
      //   .catch((error) => {
      //     console.error("Error:", error);
      //     // Handle any errors
      //   });
    }
     if(window.localStorage.getItem("currentProject_id")){

      //CallApi('subtitle/eachClassLearnProject/', setStates, '', window.localStorage.getItem("currentProject_id"));
    }
    // if (window.localStorage.getItem("states")) {
    //   setStates(JSON.parse(window.localStorage.getItem("states")));
    // }
    if (currentProject) {
      CallApi('subtitle/eachClassLearnProject/', setStates, '', currentProject.id)

    }
  }, []);
  useEffect(() => {
    if (states) {
      window.localStorage.setItem("states", JSON.stringify(states));
      window.localStorage.setItem("currentOrder", order);
    }
  }, [states]);
  useEffect(() => {
    if (states) {
      window.localStorage.setItem("currentOrder", order);
    }
  }, [order]);
  // useEffect(() => {
  //   if (currentProject) {
  //     window.localStorage.setItem("currentProject", JSON.stringify(currentProject));
  //   }
  // }, [currentProject]);
  return (
    <Container>
      <YTSize>
        <YoutubeIframe
          videoId={videoId}
          startTime={0}
          player={player}
          setPlayer={setPlayer}
        />
      </YTSize>
      <StageButtonContainer>
        <PreviousButton
          player={player}
          setOrder={setOrder}
          order={order}
          states={states} />
        <Video />
        <NextButton
          player={player}
          setOrder={setOrder}
          order={order}
          states={states} />
      </StageButtonContainer>

      <RecordingButton
        player={player}
        setOrder={setOrder}
        order={order}
        states={states}
        currentProject={currentProject} />

      <Subtitle type={0} states={states} order={order} currentProject={currentProject} setCurrentProject={setCurrentProject} />
      <Subtitle type={1} states={states} order={order} currentProject={currentProject} setCurrentProject={setCurrentProject}/>
      <ButtonsContainer>
      <Button next="/Home" color="999999" title=""></Button>
      <Button next="/Achievement"color="46b049" title=""></Button>
      {/* <Button color="5397e3" title=""></Button> */}
      </ButtonsContainer>
    </Container>
  );
}

export default Body;
