import styled from "styled-components";
import logo from "../images/Logo.png";
import { BsGearFill, BsPlusCircleFill } from "react-icons/bs";
import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { default as OffCanvas } from "./_OffCanvas";
import { useAudioRecorder } from "react-audio-voice-recorder";
const Container = styled.div`
  width: 100%;
  background: rgb(255, 135, 115);
  display: flex;
  justify-content: space-between;
`;
const Logo = styled.img`
  cursor: pointer;
`;
const Buttons = styled.div`
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  display: ${(props) => (props.type === "Home" ? "flex" : "none")};
  @media screen and (max-width: 700px) {
    display: none;
  }
`;

const ButtonNew = styled(Link)`
  text-decoration: none;
  margin: 0.1em;
  background: linear-gradient(#29afd0, #008db0);
  border-radius: 5px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.5);
  color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 1.75em;
  cursor: pointer;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.5);
  transition: box-shadow 0.1s;
  height: 50px;
  width: 150px;
  display: flex;
  & p {
    font-size: 1.375rem;
    font-weight: 600;
    margin-top: -1px;
    margin-left: 10px;
  }
`;

const ButtonNew2 = styled(ButtonNew)`
  width: 125px;
  height: 50px;
  border-radius: 0;
  box-shadow: none;
  border-bottom: 1px solid #aaaaaa;
  margin: 0;
`;
const ButtonUser = styled.div`
  text-decoration: none;
  margin: 0.2em;
  color: #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: ${22 / 16}rem;
  font-weight: bold;
  margin: 0 1rem;
  cursor: default;
`;
const ButtonUserBook = styled(Link)`
  color: rgb(65 74 201);

  width: 100%;
  color: #fff;
  font-weight: 600;
  font-size: 35px;
  z-index: 2;
  right: 0;
  left: 0;
  top: 20px;
  position: absolute;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  ${(props) =>
    props.type === "NewProject" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "achievement" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "recording" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "Home" &&
    `
    display: none;
  `}
  @media screen and (max-width: 700px) {
    position: static;
  }
  @media screen and (max-width: 560px) {
    font-size: 20px;
  }
`;

const ButtonLogOut = styled.div`
  text-decoration: none;
  color: #fff;
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: 1.75em;
  margin: 0 1rem;
  & p {
    font-size: 1.375rem;
    font-weight: 600;
    margin: auto;
  }
  :hover {
    text-decoration: underline;
  }
`;
const Title = styled.div`
  width: 100%;
  color: #fff;
  font-weight: 600;
  font-size: 35px;
  z-index: 2;
  right: 0;
  left: 0;
  top: 20px;
  position: absolute;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  ${(props) =>
    props.type === "NewProject" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "achievement" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "recording" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "Home" &&
    `
    display: none;
  `}
  @media screen and (max-width: 700px) {
    position: static;
  }
  @media screen and (max-width: 560px) {
    font-size: 20px;
  }
`;
const Title2 = styled(Link)`
  text-decoration: none;
  align-items: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 1rem;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;
  font-weight: 600;
  z-index: 2;
  align-items: center;
  ${(props) =>
    props.type === "NewProject" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "achievement" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "recording" &&
    `
  font-size: 1.5rem;

    display: flex;
  `}
  ${(props) =>
    props.type === "Home" &&
    `
    display: none;
  `}
  @media screen and (max-width: 700px) {
    position: static;
  }
  @media screen and (max-width: 560px) {
    font-size: 20px;
  }
`;
const TestRecordContainer = styled.div`
  width: 30vw;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const TestRecord = styled.div`
  width: 7.5vw;
  height: 36px;
  background: #ec8500;
  color: #fff;
  border-radius: 10px;
  font-size: 1rem;
  margin: 1rem;
  padding: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  cursor: pointer;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;
  &:hover {
    box-shadow: 0px 0px 0px 8px rgba(25, 118, 210, 0.16);
  }
  @media screen and (max-width: 380px) {
    position: static;
    margin: 0;
  }
`;

const TestRecordPlay = styled.div`
  width: 7.5vw;
  height: 36px;
  background: #ec8500;
  color: #fff;
  border-radius: 5px;
  font-size: 1rem;
  margin: 1rem;
  padding: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  cursor: pointer;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;
  &:hover {
    box-shadow: 0px 0px 0px 8px rgba(25, 118, 210, 0.16);
  }
  @media screen and (max-width: 380px) {
    position: static;
    margin: 0;
  }
  ${(props) =>
    props.play ?`  display: flex;`:`
    display: none;
    `
  }
`;

const UserBook = styled.div`
  width: 15vw;
  height: 36px;
  color: #fff;
  border-radius: 5px;
  font-size: 1rem;
  justify-content: center;
  align-items: center;
  z-index: 1;
  cursor: pointer;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;
  &:hover {
    box-shadow: 0px 0px 0px 8px rgba(25, 118, 210, 0.16);
  }
  @media screen and (max-width: 380px) {
    position: static;
    margin: 0;
  }
`;

const Title3 = styled(Link)`
  text-decoration: none;
  width: 140px;
  height: 50px;

  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 1rem;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;

  width: 100%;
  font-weight: 600;
  z-index: 2;
  right: 0;
  left: -30px;
  top: 20px;
  position: absolute;
  justify-content: end;
  align-items: center;
  ${(props) =>
    props.type === "NewProject" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "achievement" &&
    `
    display: flex;
  `}
  ${(props) =>
    props.type === "recording" &&
    `
  font-size: 1.5rem;

    display: flex;
  `}
  ${(props) =>
    props.type === "Home" &&
    `
    display: none;
  `}
  @media screen and (max-width: 700px) {
    position: static;
  }
  @media screen and (max-width: 560px) {
    font-size: 20px;
  }
`;
const Line = styled.div`
  width: 2px;
  height: 3rem;
  background: linear-gradient(transparent, #fff, transparent);
`;
const MenuButton = styled.div`
  width: 26px;
  height: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  div {
    width: 26px;
    height: 3px;
    background: #fff;
  }
`;
const MenuContainer = styled.div`
  display: none;
  position: relative;
  justify-content: center;
  align-items: center;
  padding-right: 20px;
  @media screen and (max-width: 700px) {
    display: ${(props) => (props.type === "Home" ? "flex" : "none")};
  }
`;
const MenuTransition = styled.div`
  display: flex;
  position: absolute;
  top: 80px;
  right: 0;
  width: 125px;
  overflow: hidden;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.5);
  height: ${(props) => (props.$show ? "150px" : "0")};
  transition: height 0.3s;
`;
const Menu = styled.div`
  display: flex;
  width: 125px;
  height: 150px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #fff;
  div {
    width: 100%;
    font-size: ${18 / 16}rem;
    font-weight: bold;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #aaaaaa;
    padding: 0 1rem;
  }
  div:last-of-type {
    cursor: pointer;
    border-bottom: none;
    :hover {
      text-decoration: underline;
    }
  }
`;
function Header(props) {
  const {
    isLoggedIn,
    setIsLoggedIn,
    type,
    content,
    currentProject,
    setCurrentProject,
  } = props;
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isPlay, setIsPlay] = useState(false);
  const {
    startRecording,
    stopRecording,
    togglePauseResume,
    recordingBlob,
    isRecording,
    isPaused,
    recordingTime,
    mediaRecorder,
    
  } = useAudioRecorder();

  let __timeoutId;
  useEffect(() => {
    if (window.localStorage.getItem("currentProject_id")) {
      console.log(
        "currentProject_id=>" + window.localStorage.getItem("currentProject_id")
      );
    }

    if (window.localStorage.getItem("states")) {
      console.log(
        "states=>" + JSON.stringify(window.localStorage.getItem("states"))
      );
    }
    if (window.localStorage.getItem("currentProject")) {
      console.log(
        "currentProject=>" +
          JSON.stringify(window.localStorage.getItem("currentProject"))
      );
    }
    
  }, []);
  const navigate = useNavigate();
  const handlePageClick = (e) => {
    e.preventDefault();
    if (isLoggedIn) {
      setIsLoggedIn(false);
      window.localStorage.removeItem("user");
      window.localStorage.removeItem("isLoggedIn");
    }
  };
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };
  const addAudioElement = (blob) => {
    const url = URL.createObjectURL(blob);
    const testAudio = document.createElement("testingAudio");
    testAudio.src = url;
    testAudio.controls = true;
  };
  const handleRecordingStart = (e)=>{
    e.preventDefault();
    startRecording();
    __timeoutId = setTimeout(() => {
      togglePauseResume();
    }, 8000);


  };

  const handleRecordPlay = (e) => {
    e.preventDefault();
if(document.getElementById('testAudioRecordPlayer').src){
  document.getElementById('testAudioRecordPlayer').play();
}
  };
  
  const handleRecordingStop = () => {
      stopRecording();
      clearTimeout(__timeoutId);
    // }
  };
  useEffect(() => {
    if (isPaused) {
      console.log("isPaused record");
      handleRecordingStop();
    }
  }, [isPaused]);

  useEffect(() => {
    console.log('stopRecording record');
    if (!recordingBlob) {
if(document.getElementById('testAudioRecordPlayer')){

  // document.getElementById('testAudioRecordPlayer').src="";
  // document.getElementById('testAudioRecordPlayer').hidden=true;
  
}


      return;
    }
    if(recordingBlob){
      const url = URL.createObjectURL(recordingBlob);
     // const __data = getWaveBlob(recordingBlob,true);
     let  testAudioRecordPlayer=document.getElementById('testAudioRecordPlayer');
     testAudioRecordPlayer.src = url;
     setIsPlay(true);
      }
    
    },[recordingBlob]);
  return (
    <Container>
      <Logo src={logo} onClick={() => navigate("/Home")} />
      <Buttons type={type}>
        <ButtonNew
          title=""
          to="/NewProject"
          onClick={() => {
            window.localStorage.setItem("currentProject", null);
            setCurrentProject(null);
          }}
          type={type}
        >
          <BsPlusCircleFill />
          <p></p>
        </ButtonNew>
        <ButtonUser type={type}>
          <p>
            {isLoggedIn &&
              "hi, " + JSON.parse(window.localStorage.getItem("user")).username}
          </p>
        </ButtonUser>
        <Line />
        <ButtonLogOut onClick={handlePageClick} type={type} title="">
          <p></p>
        </ButtonLogOut>
      </Buttons>
      <MenuContainer type={type}>
        <MenuButton onClick={toggleMenu}>
          <div></div>
          <div></div>
          <div></div>
        </MenuButton>
        <MenuTransition $show={isMenuOpen}>
          <Menu>
            <ButtonNew2
              title=""
              to="/NewProject"
              onClick={() => {
                window.localStorage.setItem("currentProject", null);
                setCurrentProject(null);
              }}
              type={type}
            >
              <BsPlusCircleFill />
              <p></p>
            </ButtonNew2>
            <div>
              <p>
                {isLoggedIn &&
                  JSON.parse(window.localStorage.getItem("user")).username}
              </p>
            </div>
            <div onClick={handlePageClick} type={type} title="">
              <p></p>
            </div>
          </Menu>
        </MenuTransition>
      </MenuContainer>

      <Title type={type}>{content}</Title>

      {type == "recording" && (
        <TestRecordContainer>
          <TestRecord  onClick={handleRecordingStart}></TestRecord>
          <TestRecordPlay play={isPlay} onClick={handleRecordPlay} ></TestRecordPlay>
          <UserBook>
            <Title2
              target="_blank"
              to="/ c94e80cc0c024061bf0da584685cffd8.html"
              type={type}
            >
              
            </Title2>
            <audio id="testAudioRecordPlayer" volume={50} hidden ></audio>
          </UserBook>
        </TestRecordContainer>
      )}
    </Container>
  );
}

export default Header;
