import styled from "styled-components";
import { useState, useEffect } from "react";
import banner from "../images/banner_01.png";
import { BsFillCheckCircleFill } from "react-icons/bs";
import { useNavigate } from "react-router-dom";
import { getAll, getProjectPage, createItem, updateItem } from "../API/Api";
const Container = styled.div`
  width: 100%;
  height: calc(100% - 110px);
  background-color: #faf8f6;
  padding-top: 50px;
  padding-bottom: 100px;
  font-weight: 600;
  @media screen and (max-width: 790px) {
    padding-top: 0;
  }
`;
const InfoContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 790px) {
    flex-direction: column;
  }
`;
const LeftContainer = styled.div`
  display: flex;
  justify-content: top;
  align-items: flex-end;
  flex-direction: column;
  border-right: 2px solid #dcd7d2;
  height: 600px;
  @media screen and (max-width: 790px) {
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
    border-right: none;
    border-bottom: 2px solid #dcd7d2;
  }
`;
const Banner = styled.div`
  background: url(${banner});
  width: 240px;
  height: 65px;
`;
const Navs = styled.div`
display: flex;
  justify-content: top;
  align-items: flex-end;
  flex-direction: column;
  @media screen and (max-width: 790px) {
    flex-direction: row;
  }
`
const Nav = styled.div`
  margin: 5px 20px;
  font-size: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  &:hover {
    color: #cb4832;
  }
  @media screen and (max-width: 790px) {
    display: none;
  }
`;
const Icon = styled.div`
display: flex;
justify-content: center;
align-items: center;
  margin-left: 15px;
  color: #faf8f6;
  ${(props) =>
    props.active &&
    `
    color: #dcd7d2;
`}
  @media screen and (max-width:790px) {
    margin-left: 5px;
  }
`;
const RightContainer = styled.div`
  // position: relative;
  height: 600px;
  padding: 0 40px;
  @media screen and (max-width: 790px) {
    font-size: 20px;
    padding: 0;
  }
`;
const List = styled.div`
  padding: 13px 0;
  font-size: 22px;
  border-bottom: 2px dotted #dcd7d2;
  width: 100%;
  max-width: 444px;
  cursor: pointer;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  &:hover {
    color: #cb4832;
  }
  @media screen and (max-width: 790px) {
    max-width: 370px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
`;
const PageNumContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  // position: absolute;
  width: 100%;
  bottom: -60px;
  right: 0;
  user-select: none;
`;
const PageNum = styled.div`
  cursor: pointer;
  margin: 10px;
  font-size: 22px;
  ${(props) =>
    props.active &&
    `
color: #cb4832;
`}
`;
const Select = styled.select`
display: none;
@media screen and (max-width: 790px) {
  display: block;
  width: 100%;
  max-width: 370px;
  font-size: 22px;
  margin: 0 auto 1rem auto;
  font-weight: bold;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: 1px solid #dcdcdc;
}
@media screen and (max-width: 380px) {
  margin: 0 1rem 1rem 1rem;
}
`
function HomePageBody(props) {
  const { currentProject, setCurrentProject } = props
  const [selectedGrade, setSelectedGrade] = useState(1);
  const [projectLists, setProjectLists] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPageCount, setTotalPageCount] = useState(0);
  const pageSize = 10;
  const navigate = useNavigate();
  useEffect(() => {
    console.log('in HomePageBody ')
      }, []);
  useEffect(() => {
let user= JSON.parse(window.localStorage.getItem("user"));
    // fetchPorject(selectedGrade, currentPage);
    const grade_response = getProjectPage("eachClassLearnProject",user.id, selectedGrade, currentPage, pageSize,1)
      .then((response) => {
        if(response.content){
          setProjectLists(response.content);
        }
        setTotalPageCount(response.totalPages);
        // Process the created item
      })
      .catch((error) => {
        console.error("Error:", error);
        // Handle any errors
      });
  }, [selectedGrade, currentPage]);


  const handleGradeClick = (grade) => {
    setSelectedGrade(grade);
    setCurrentPage(1);
  };

  const handlePageClick = (page) => {
    setCurrentPage(page);
  };
  const handleListClick = (e) => {
    // const i = Number(e.target.parentNode.parentNode.id);
    const i = Number(e.target.id);
    console.log(" id=====>>>>"+i);
    setCurrentProject(projectLists.filter(s => s.id === i)[0]);
    window.localStorage.setItem("currentProject_id",i);
    window.localStorage.setItem("currentProject",JSON.stringify( projectLists.filter(s => s.id === i)[0]));
    navigate('/NewProject');
  };

  return (
    <Container>
      <InfoContainer>
        <LeftContainer>
          <Banner />
          <Navs>
          <Nav onClick={() => handleGradeClick(1)}>
           : 
                      <Icon active={selectedGrade === 1}>
              <BsFillCheckCircleFill />
            </Icon>
          </Nav>
          </Navs>
          <Select>
            <option selected> : </option>
          </Select>
        </LeftContainer>
        <RightContainer>
          {(projectLists!=null&&projectLists.length>0)? projectLists.map((single) => (
            <List title={single.learnProjectName} key={single.id} onClick={handleListClick} id={single.id}>
              {single.learnProjectName}
            </List>
          )):<List  >
          {""}
        </List>}

          {totalPageCount > 1 && (
            <PageNumContainer>
              {Array.from(
                { length: totalPageCount },
                (_, index) => index + 1
              ).map((page) => (
                <PageNum active={currentPage===page} key={page} onClick={() => handlePageClick(page)}>
                  {page}
                </PageNum>
              ))}
            </PageNumContainer>
          )}
        </RightContainer>
      </InfoContainer>
    </Container>
  );
}

export default HomePageBody;