// App.js

import React, { useState, useEffect } from "react";
import { User, Token } from "./components/Auth";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
  useNavigate,
} from "react-router-dom";
import axios from "axios";
import LoginPage from "./LoginPage";
import HomePage from "./HomePage";
import NewProjectPage from "./NewProjectPage";
import RecordingPage from "./RecordingPage";
import AchievementPage from "./AchievementPage";
import Header from "../src/components/Header";
import { useCookies } from "react-cookie";
import { GoogleOAuthProvider } from '@react-oauth/google';

function App() {
  const [currentProject, setCurrentProject] = useState([]);
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const [user, setUser] = useState();
  const [token, settoken] = useState([]);
  const [cookies, setCookie, removeCookie] = useCookies(["cookie-name"]);
  // 
  useEffect(() => {
    console.log("print");
  }, [isLoggedIn]);
  useEffect(() => {
    console.log("in App");
    //setIsLoggedIn(true);
  }, [user]);

  useEffect(() => {
    //setIsLoggedIn(true);
    if (
      window.localStorage.getItem("isLoggedIn") &&
      window.localStorage.getItem("isLoggedIn") == "true"
    ) {
      setIsLoggedIn(true);
    } else {
      setIsLoggedIn(false);
    }
  }, []);

  return (
    
    <Router basename="/tai_gi">
      {/* <Header isLoggedIn={isLoggedIn} user={user} onLogout={handleLogout} /> */}
      <Routes>
        {/* <Route
          exact
          path="login"
          element={<LoginPage isLoggedIn={isLoggedIn} setUser={setUser} setIsLoggedIn={setIsLoggedIn}  user={user} />}
        /> */}
 <Route
          exact
          path="/"
          element={
            isLoggedIn ? (
              
              <Navigate to="/home" />
            ) : (
              
              <LoginPage
                isLoggedIn={isLoggedIn}
                setUser={setUser}
                setIsLoggedIn={setIsLoggedIn}
                user={user}
              />
            )
          }
        />
        <Route
          exact
          path="*"
          element={
            isLoggedIn ? (
              <Navigate to="/home" />
            ) : (
              <LoginPage
                isLoggedIn={isLoggedIn}
                setUser={setUser}
                setIsLoggedIn={setIsLoggedIn}
                user={user}
              />
            )
          }
        />
        <Route
          exact
          path="/home"
          element={
            isLoggedIn ? (
              <HomePage
                isLoggedIn={isLoggedIn}
                setIsLoggedIn={setIsLoggedIn}
                user={user}
                currentProject={currentProject}
                setCurrentProject={setCurrentProject}
              />
            ) : (
              <Navigate to="/login" />
            )
          }
        />

        <Route
          exact
          path="/newproject"
          element={
            isLoggedIn ? (
              <NewProjectPage
                isLoggedIn={isLoggedIn}
                setIsLoggedIn={setIsLoggedIn}
                user={user}
                currentProject={currentProject}
                setCurrentProject={setCurrentProject}
              />
            ) : (
              <Navigate to="login" />
            )
          }
        />
        <Route
          exact
          path="/recording"
          element={
            isLoggedIn ? (
              <RecordingPage
                isLoggedIn={isLoggedIn}
                setIsLoggedIn={setIsLoggedIn}
                user={user}
                currentProject={currentProject}
                setCurrentProject={setCurrentProject}
              />
            ) : (
              <Navigate to="/login" />
            )
          }
        />

        <Route
          exact
          path="achievement"
          element={
            isLoggedIn ? (
              <AchievementPage
                isLoggedIn={isLoggedIn}
                setIsLoggedIn={setIsLoggedIn}
                user={user}
                currentProject={currentProject}
                setCurrentProject={setCurrentProject}
              />
            ) : (
              <Navigate to="login" />
            )
          }
        />
      </Routes>
    </Router>
  );
}

// 
function PrivateRoute({ component: Component, isLoggedIn, user, ...rest }) {
  return (
    <Route
      {...rest}
      render={(props) =>
        isLoggedIn ? (
          <Component {...props} user={user} />
        ) : (
          <Navigate to="/login" />
        )
      }
    />
  );
}

export default App;
