import styled from "styled-components";
import { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { useNavigate, r } from "react-router-dom";
import { getAll, getItemById, createItem, updateItem } from "../API/Api";
import { useAlert } from "react-alert";
const Container = styled.div`
  position: relative;
  width: 100%;
  height: calc(100vh - 110px);
  font-weight: 600;
  font-size: 22px;
  background: #faf8f6;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-items: top;
  padding: 2rem 0 0 0;
  flex-direction: column;
  @media screen and (max-width: 480px) {
    padding: 0;
  }
`;
const ButtonArea = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
`;
const Button = styled(Link)`
  text-decoration: none;
  width: 140px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  background: #529e28;
  margin: 0 1rem;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;
  ${(props) =>
    props.color === "blue" &&
    `
background: #4e8cc6;
`}
  &:hover {
    box-shadow: 0px 0px 0px 8px rgba(25, 118, 210, 0.16);
  }
`;
const Button_Go_Record = styled(Link)`
  text-decoration: none;
  width: 140px;
  height: 50px;
  
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  background: #529e28;
  margin: 0 1rem;
  box-shadow: 0px 0px 0px 0px rgba(25, 118, 210, 0.16);
  transition: box-shadow 0.1s;
  ${(props) =>
    props.color === "blue" &&
    `
background: #4e8cc6;
`}
  ${(props) =>
    props.add === "add" ?
    `
    display: none;
`:`
display: flex;
`}
  &:hover {
    box-shadow: 0px 0px 0px 8px rgba(25, 118, 210, 0.16);
  }

`;
const Label = styled.label`
  box-shadow: 10px 0 7px -7px #cccccc;
  padding: 5px 10px;
  margin: 16px;
  margin-top: 0;
  color: #cb4832;
  width: 7rem;
  height: 2.9rem;
  @media screen and (max-width: 480px) {
    margin-bottom: 0;
  }
`;
const Input = styled.input`
  border: 1px solid #dcdcdc;
  font-weight: 600;
  font-size: 22px;
  border-radius: 5px;
  padding: 0 10px;
  width: calc(100% - 10rem);
  height: 45px;
  ::placeholder {
    color: #dddddd;
  }
  @media screen and (max-width: 480px) {
    width: 90%;
    margin: auto;
  }
`;
const Input2 = styled(Input)`
  width: 10rem;
  @media screen and (max-width: 480px) {
    margin: 0;
  }
`;
const Textarea = styled.textarea`
  border: 1px solid #dcdcdc;
  font-weight: 600;
  font-size: 22px;
  border-radius: 5px;
  padding: 0 10px;
  width: calc(100% - 10rem);
  ::placeholder {
    color: #dddddd;
  }
  @media screen and (max-width: 480px) {
    width: 90%;
    margin: auto;
  }
`;
const Form = styled.form`
  width: 100%;
  max-width: 580px;
  div {
    display: flex;
    padding: 0.5rem 0;
  }
  @media screen and (max-width: 480px) {
    div {
      flex-direction: column;
      div {
        div {
          flex-direction: row;
        }
      }
    }
  }
`;
const FlexColumn = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: calc(100% - 10rem);
  div {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    width: 100%;
  }
  @media screen and (max-width: 480px) {
    width: 100%;
    div {
    }
  }
`;
const Actor = styled.p`
  text-align: right;
  width: 7rem;
  padding: 0 1rem 0 0;
`;
const ProjectTitle = styled.p`
  @media screen and (max-width: 480px) {
    padding: 0 1rem 0 1.5rem;
  }
`;
class voiceActorObj {
  constructor(voiceCharacter, voiceActor) {
    this.voiceCharacter = voiceCharacter || "";
    this.voiceActor = voiceActor || "";
  }
}
class voiceActorsShow {
  constructor(voiceCharacter, voiceActor, id) {
    this.id = id || 0;
    this.voiceCharacter = voiceCharacter || "";
    this.voiceActor = voiceActor || "";
  }
}
class eduStage {
  constructor(id) {
    this.id = id || 1;
  }
}
class grade {
  constructor(id) {
    this.id = id || 1;
  }
}
class eachClassLearnProject {
  constructor(
    id,
    learnProjectTitle,
    learnProjectName,
    learnProjectDescription,
    learnResourceLink,
    grade,
    eduStage,
    user
  ) {
    this.id = id || 0;
    this.learnProjectTitle = learnProjectTitle || "";
    this.learnProjectName = learnProjectName || "";
    this.learnProjectDescription = learnProjectDescription || "";
    this.learnResourceLink = learnResourceLink || "";
    this.grade = grade || 1;
    this.eduStage = eduStage || 1;
    this.voiceActors = [];
    this.user = user;
  }
  addVoiceActor(voiceActor) {
    this.voiceActors.push(voiceActor);
  }
}
