import GoogleLogin from "react-google-login";

import { useState, useEffect } from "react";
import { useAlert } from "react-alert";
import { User, Token } from "./components/Auth";
import { useNavigate } from "react-router-dom";
import { loginG } from "./API/AuthApi";
import styled from 'styled-components'
const _GoogleLogin= styled(GoogleLogin)`
geig
`;

function _GoogleLoginButton(props) {
  const alert = useAlert();
  const { isLoggedIn, setIsLoggedIn, user, setUser } = props;
  const navigate = useNavigate();
  const responseGoogleFailure = (response) => {
    // Handle the response from Google Login.
    // The `response` object contains user information like name, email, and profile picture.
    console.log(response);
    alert.error(":" + response);
  };
  const responseGoogleSuccess = (response) => {
    // Handle the response from Google Login.
    // The `response` object contains user information like name, email, and profile picture.
    console.log(response);
    if (response.profileObj) {



      let rep_user = response.profileObj;
      let authUser = new User(
        response.profileObj.email,
        null,
        response.profileObj.username,
        null,
        0,
        response.profileObj.familyName,
        response.profileObj.givenName,
        response.profileObj.email,
        response.profileObj.googleId
      );

      let proj = loginG(authUser)
        .then((_response) => {
          if (_response && _response.id) {
            setIsLoggedIn(true);
            let _rep_user = _response;
            let _authUser = new User(
              _rep_user.email,
              "",
              _rep_user.username,
              _rep_user.roles,
              _rep_user.id,
              _rep_user.familyName,
              _rep_user.givenName,
              _rep_user.email,
              _rep_user.googleId
            );
            setIsLoggedIn(true);

            setUser(_authUser);
            console.log("data:" + JSON.stringify(_authUser));

            window.localStorage.setItem("user", JSON.stringify(_authUser));
            window.localStorage.setItem("isLoggedIn", "true");

            navigate("/home");
          } else {
            alert.error("google !");
          }
        })
        .catch((error) => {
          console.error("Error:", error);
          alert.error("!");
          // Handle any errors
        });
    }
  };

  return (
    <_GoogleLogin

      clientId="103986831875-2g119b3gu561ris0v2ak59a9k3uc7n0t.apps.googleusercontent.com"
      buttonText=" Google "
      onSuccess={responseGoogleSuccess}
      onFailure={responseGoogleFailure}
      theme="dark"
    />
  );
}

export default _GoogleLoginButton;
