export default function CallApi (props,Fn,Fn2,id) {
    fetch('https://bird-social-hopelessly.ngrok-free.app/' + props + (id? id:''),{})
    .then(response => {
        console.log(response)
        return response.json()
    })
    .then(data => {
        if(Fn){Fn(data)}
        if(Fn2){Fn2(data)}
    })
    .catch(error => {
        console.log(error)
    })
}