import styled from 'styled-components'
import Header from './components/Header';
import Body from './components/HomePageBody';

const Container = styled.div`
`

function HomePage(props) {
  const {isLoggedIn,setIsLoggedIn,user, currentProject, setCurrentProject } = props
  return (
    <Container>
      <Header isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn} currentProject={currentProject} setCurrentProject={setCurrentProject} type='Home'/>
      <Body  currentProject={currentProject} setCurrentProject={setCurrentProject} />
    </Container>
  );
}

export default HomePage;
