export function RecordingButton(props) {
  const { player, setOrder, order, states } = props;
  const [volume_1, setVolume_1] = useState(50);
  const [volume_2, setVolume_2] = useState(50);
  const [volume_3, setVolume_3] = useState(50);

  let _isRecording = 0; // 0 1 2 
  let audioPlayer_current;
  let audioPlayer_bak;
  let audioPlayer_originalVoice; // 
  const [pid, setpid] = useState(
    window.localStorage.getItem("currentProject_id")
  );

  let timeoutId;
  const {
    startRecording,
    stopRecording,
    togglePauseResume,
    recordingBlob,
    isRecording,
    isPaused,
    recordingTime,
    mediaRecorder,
  } = useAudioRecorder();
  useEffect(() => {
    audioPlayer_current = document.getElementById("audioPlayer_current");
    audioPlayer_bak = document.getElementById("audioPlayer_bak");
    audioPlayer_originalVoice = document.getElementById(
      "audioPlayer_originalVoice"
    );
    setpid(window.localStorage.getItem("currentProject_id"));
    document.getElementById("RecordingStopBtn").hidden = true;
    if (!order) {
      setOrder(0);
    }
    // if(states&&order){
    //     audioPlayer_current.src=checkLink(
    //         states[order].subtitleVoiceLink,
    //         pid,
    //         states[order].sort
    //       );
    // }
  }, []);
  // 
  const stopVideo = (time) => {
    audioPlayer_bak = document.getElementById("audioPlayer_bak");
    audioPlayer_originalVoice = document.getElementById(
      "audioPlayer_originalVoice"
    );
    setTimeout(() => {
      audioPlayer_bak.pause();
      audioPlayer_originalVoice.pause();
      player.pauseVideo();
      _isRecording = 0;
    }, time);
  };

  const playRecording = () => {
    document.getElementById("audioPlayer_current").play();
  };

  // 
  const handleRecordingStart = () => {
    audioPlayer_bak = document.getElementById("audioPlayer_bak");
    audioPlayer_originalVoice = document.getElementById(
      "audioPlayer_originalVoice"
    );
    let now = order;
    // 
    if (_isRecording === 0) {
      document.getElementById("RecordingBtn").hidden = true;
      document.getElementById("RecordingStopBtn").hidden = false;
      player.seekTo(time(states[now].startTime));
      audioPlayer_bak.currentTime = time(states[order].startTime);
      audioPlayer_originalVoice.currentTime = time(states[order].startTime);
      // 
      player.mute(); // 
      audioPlayer_bak.play();
      audioPlayer_originalVoice.play();
      player.playVideo();
      _isRecording = 1;
      startRecording();
      // ,
      if (now === states.length - 1) {
        let _start_time =
          Number(time(states[now].endTime) - time(states[now].startTime)) *
          1000;

        timeoutId = setTimeout(() => {
          document.getElementById("RecordingBtn").hidden = false;
          document.getElementById("RecordingStopBtn").hidden = true;
          audioPlayer_bak.pause();
          audioPlayer_originalVoice.pause();
          player.pauseVideo();
          togglePauseResume();
        }, _start_time);
        // startRecording(
        //     Number(time(states[now].endTime) - time(states[now].startTime)) * 1000
        // );
      } else {
        let _start_time =
          Number(
            time(states[now].endTime) - time(states[now].startTime)
          ) * 1000;
        timeoutId = setTimeout(() => {
          document.getElementById("RecordingBtn").hidden = false;
          document.getElementById("RecordingStopBtn").hidden = true;
          audioPlayer_bak.pause();
          audioPlayer_originalVoice.pause();
          player.pauseVideo();
          togglePauseResume();
        }, _start_time);
        // startRecording(
        //     Number(
        //         time(states[now + 1].startTime) - time(states[now].startTime)
        //     ) * 1000
        // );
        // stopVideo(
        //   Number(
        //     time(states[now + 1].startTime) - time(states[now].startTime)
        //   ) * 1000
        // );
      }
    }
  };

  //
  const handleRecordingStop = () => {
    audioPlayer_bak = document.getElementById("audioPlayer_bak");
    audioPlayer_originalVoice = document.getElementById(
      "audioPlayer_originalVoice"
    );
    // let now = order;
    // 
    // if (_isRecording !== 0) {
    document.getElementById("RecordingBtn").hidden = false;
    document.getElementById("RecordingStopBtn").hidden = true;
    player.pauseVideo();
    audioPlayer_bak.pause();
    audioPlayer_originalVoice.pause();

    stopRecording();
    clearTimeout(timeoutId);
    // }
  };
  // 
  const handleListen = () => {
    if (_isRecording === 0) {
      playRecording();
      audioPlayer_bak = document.getElementById("audioPlayer_bak");
      audioPlayer_originalVoice = document.getElementById(
        "audioPlayer_originalVoice"
      );
      audioPlayer_bak.currentTime = time(states[order].startTime);
      audioPlayer_originalVoice.currentTime = time(states[order].startTime);
      audioPlayer_bak.play();
      audioPlayer_originalVoice.play();
      player.seekTo(time(states[order].startTime));
      player.mute();
      player.playVideo();
      _isRecording = 3;
      stopVideo(
        Number(
          time(states[order + 1].startTime) - time(states[order].startTime)
        ) * 1000
      );
    }
  };
  useEffect(() => {
    if (isPaused) {
      console.log("isPaused record");
      handleRecordingStop();
    }
  }, [isPaused]);
  useEffect(() => {
    console.log("stopRecording record");
    if (!recordingBlob) return;
    if (recordingBlob) {
      const url = URL.createObjectURL(recordingBlob);
      // const __data = getWaveBlob(recordingBlob,true);
      const testAudio = document.getElementById("audioPlayer_current");
      testAudio.src = url;
      //testAudio.controls = true;
      blobToBase64(recordingBlob).then((data) => {
        let currentOrder_ = window.localStorage.getItem("currentOrder");
        let current_states_ = JSON.parse(window.localStorage.getItem("states"));
        if (currentOrder_ && current_states_) {
          let sub = current_states_[currentOrder_];

          // let updateSub=new SubtitleAudioData(sub.id,data.replace("data:audio/wav;base64,"));

          let _audio = updateAudio(
            sub.id,
            data.replace("data:audio/webm;codecs=opus;base64,", "")
          )
            .then((response) => {
              console.log(JSON.stringify(response));
            })
            .catch((error) => {
              console.error("Error:", error);
              // Handle any errors
            });
        }
        console.log(data);
      });
    }
  }, [recordingBlob]);
  function blobToBase64(blob) {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.onload = (e) => {
        resolve(e.target.result);
      };
      // readAsDataURL
      fileReader.readAsDataURL(blob);
      fileReader.onerror = () => {
        reject(new Error("blobToBase64 error"));
      };
    });
  }
  function checkLink(subtitleVoiceLink, id, sort) {
    if (subtitleVoiceLink.includes("type")) {
      return (
        process.env.REACT_APP_API_URL +
        "/subtitle/audio/" +
        id +
        "/sort/" +
        sort
      );
    } else {
      return process.env.REACT_APP_API_URL + "/subtitle/Silent/" + sort;
    }
  }
  //
  useEffect(() => {
    audioPlayer_bak = document.getElementById("audioPlayer_current");
    if (audioPlayer_bak) {
      audioPlayer_bak.volume = volume_3 / 100;
    }
  }, [volume_3]);
  useEffect(() => {
    audioPlayer_bak = document.getElementById("audioPlayer_bak");
    if (audioPlayer_bak) {
      audioPlayer_bak.volume = volume_1 / 100;
    }
  }, [volume_1]);
  useEffect(() => {
    audioPlayer_originalVoice = document.getElementById(
      "audioPlayer_originalVoice"
    );
    if (audioPlayer_originalVoice) {
      audioPlayer_originalVoice.volume = volume_2 / 100;
    }
  }, [volume_2]);
  return (
    <Container>
      <RecordingButtonContainer>
        <PreviousButton
          player={player}
          setOrder={setOrder}
          order={order}
          states={states}
        />
        <Recording
          id="RecordingBtn"
          title=""
          onClick={handleRecordingStart}
        />
        <RecordingStop
          id="RecordingStopBtn"
          title=""
          onClick={handleRecordingStop}
        ></RecordingStop>
        <NextButton
          player={player}
          setOrder={setOrder}
          order={order}
          states={states}
        />
      </RecordingButtonContainer>
      <VoicePlayeContainer>
        <PlayerButton title="" onClick={handleListen}>
          <div></div>
        </PlayerButton>
        <VolumeContainer>
          <p></p>
          <Slider1
            value={volume_1}
            onChange={(e) => setVolume_1(Number(e.target.value))}
            sx={{ width: 90, color: "#80c24e", height: 7 }}
          />
        </VolumeContainer>
        <VolumeContainer>
          <p></p>
          <Slider1
            value={volume_2}
            onChange={(e) => setVolume_2(Number(e.target.value))}
            sx={{ width: 90, color: "#80c24e", height: 7 }}
          />
        </VolumeContainer>
        <VolumeContainer>
          <p></p>
          <Slider1
            value={volume_3}
            onChange={(e) => setVolume_3(Number(e.target.value))}
            sx={{ width: 90, color: "#80c24e", height: 7 }}
          />
        </VolumeContainer>
      </VoicePlayeContainer>
      <AudioPlayer
        id="audioPlayer_current"
        className="audioPlayer_current"
        src={
          states
            ? order
              ? checkLink(
                  states[order].subtitleVoiceLink,
                  pid,
                  states[order].sort
                )
              : checkLink(states[0].subtitleVoiceLink, pid, 0)
            : ""
        }
        controls={true}
      />
      <AudioPlayer id="audioPlayer_bak" src={bkMusic} controls={true} />
      <AudioPlayer
        id="audioPlayer_originalVoice"
        src={voiceMusic}
        controls={true}
      />
    </Container>
  );
}

const Next = styled.button`
  width: 70px;
  cursor: pointer;
  height: 70px;
  border: none;
  &:hover {
    filter: drop-shadow(0px 0px 4px rgba(25, 118, 210, 0.6));
  }
  background: url(${i3})
    ${(props) =>
      props.notShow &&
      `
display: none;
visibility: hidden;
`};
`;
const Previous = styled(Next)`
  background: url(${i2});
`;
export function NextButton(props) {
  const { player, setOrder, order, states } = props;
  const [notShow, setNotShow] = useState(false);
  const handleNextStage = () => {
    if (order < states.length - 1 || order == 0) {
      setOrder(order + 1);
      player.seekTo(time(states[order + 1].startTime));
      player.pauseVideo();
      clearInterval(timer);
    }
  };
  useEffect(() => {
    if (states && order === states.length - 1) {
      setNotShow(true);
    } else {
      setNotShow(false);
    }
  }, [order]);
  return <Next title="" onClick={handleNextStage} notShow={notShow} />;
}

export function PreviousButton(props) {
  const { player, setOrder, order, states } = props;
  const handlePreviousStage = () => {
    if (order > 0) {
      setOrder(order - 1);
      player.seekTo(time(states[order - 1].startTime));
      player.pauseVideo();
      clearInterval(timer);
    }
  };
  return (
    <Previous
      title=""
      onClick={handlePreviousStage}
      notShow={order === 0}
    />
  );
}
