import styled from "styled-components";
import Slider from "@mui/material/Slider";
import SwitchButton from "./SwitchButton";
import { useState } from "react";
import Button from "./Button";
import YoutubeIframe from "./YoutubeIframeMute";
import { useEffect, useRef } from "react";
import CallApi from "../API/CallApi";
import bkMusic from "../media/bk.mp3"
const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: -30px 0 0 0;
  font-weight: 600;
  @media screen and (max-width: 780px) {
    margin: 0;
  }
`;
const Video = styled.div`
  position: relative;
  width: 750px;
  aspect-ratio: 750/563;
  box-shadow: 0 0 5px gray;
  @media screen and (max-width: 780px) {
    width: 500px;
  }
  @media screen and (max-width: 520px) {
    width: 365px;
  }
`;
const TestSlider = styled(Slider)`
  color: #52af77;
  height: 8px;
  width: 250px;
  & .MuiSlider-thumb {
    height: 20px;
    width: 20px;
    background: #fff;
  }
  &:nth-of-type(2) {
    margin-right: 25px;
  }
`;
const Control = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  position: static;
  & p {
    margin: 0 15px 0 20px;
  }
  @media screen and (max-width: 750px) {
    width: 365px;
    flex-wrap: wrap;
    justify-content: left;
    p {
      margin: 0 10px 0 10px;
    }
  }
`;
const ControlBk = styled.div`
  z-index: -1;
  background: #e1ddda;
  width: 100%;
  height: 100px;
  margin: -100px 0 0 0;
`;
const InfoContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: start;
  width: 100%;
  background: #f2efec;
  padding: 2rem 0;
  @media screen and (max-width: 750px) {
    flex-direction: column;
    height: 300px;
  }
`;
const Info = styled.div`
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  width: 375px;
  height: 100%;
  margin-left: 50px;
  @media screen and (max-width: 750px) {
    margin: auto;
  }
  @media screen and (max-width: 380px) {
    width: 100%;
  }
`;
const InfoText = styled.div`
  display: flex;
  justify-content: center;
  align-items: start;
  margin: 5px 0;
  font-size: 15px;
  color: #bb6051;
  p:last-of-type {
    width: calc(100% - 80px);
  }
  p:first-of-type {
    width: 80px;
  }
`;
const SubtitleContainer = styled.div`
  display: ${(props) => (props.show ? "flex" : "none")};
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  position: absolute;
  bottom: 80px;
`;
const SubtitleContainer2 = styled(SubtitleContainer)`
position: static;
background: #f2efec;
height: 3rem;
`
const Subtitle = styled.div`
  display: ${(props) => (props.show ? "block" : "none")};
  color: #fff;
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  text-align: center;
  max-width: 750px;
`;
const Subtitle2 = styled(Subtitle)`
  background: rgba(0, 0, 0, 0.3);
`
const ButtonsContainer = styled.div`
display: flex;
justify-content: center;
align-items: center;
position: fixed;
bottom: 10px;
right: 10px;
z-index: 10;
`
function TextBlock(props) {
  if (props && props.proj && props.proj.voiceActors) {
    return (
      <Info>
        <InfoText>
          {props.proj.voiceActors.map((single, i) =>
            <div>{single.voiceCharacter}&nbsp;/&nbsp;{single.voiceActor}
              {i === props.proj.voiceActors.length - 1 ? "" : ""}</div>
          )}
        </InfoText>
      </Info>
    );
  }
}

function Body(props) {
  const [currentProject, setCurrentProject] = useState(null);

  const [soundValue, setSoundValue] = useState(20);
  const [voiceValue, setVoiceValue] = useState(80);
  const [switchOne, setSwitchOne] = useState(true);
  const [switchTwo, setSwitchTwo] = useState(false);
  const [switchThree, setSwitchThree] = useState(false);
  const [data, setData] = useState("");
  const [player, setPlayer] = useState("");
  const [states, setStates] = useState("");
  const [showSubtitle, setShowSubtitle] = useState(false);
  const videoState = useRef(0);
  const timer = useRef("");
  let audio, audio2
  let videoId;
  if (currentProject) {
    videoId = currentProject.learnResourceLink.match(
      /\w+:\/\/[^/:]+[^# ]*v=([\w\-]+)/
    )[1];
  } else {
    videoId = JSON.parse(
      window.localStorage.getItem("currentProject")
    ).learnResourceLink.match(/\w+:\/\/[^/:]+[^# ]*v=([\w\-]+)/)[1];
  }
  useEffect(() => {
    //
    if (window.localStorage.getItem("currentProject")) {
      let p = JSON.parse(window.localStorage.getItem("currentProject"));
      setCurrentProject(p);
    } else {
    }

    CallApi(
      "subtitle/eachClassLearnProject/",
      setStates,
      "",
      JSON.parse(window.localStorage.getItem("currentProject")).id
    );
    if (window.localStorage.getItem("states")) {
      setStates(JSON.parse(window.localStorage.getItem("states")));
    }
  }, []);

  
  //
  useEffect(() => {
    if (player && states) {
      const videoStates = (e) => {
        if (e.data === 1) {
          console.log(":" + player.getCurrentTime());
          audio = document.getElementById("audioPlayer")
          audio2 = document.getElementById("audioPlayer_bak")
          audio.src = "https://bird-social-hopelessly.ngrok-free.app/subtitle/audio/Result/"+window.localStorage.getItem("currentProject_id");
          audio.currentTime = player.getCurrentTime()
          audio.load();
          audio.play();
          audio2.currentTime = player.getCurrentTime()
          audio2.load()
          audio2.play();
          subtitleSearch(0, states);
        } else if (e.data === 2) {
          console.log(
            ", videoState.current =" + videoState.current
          );
          audio.pause();
          audio2.pause();
          clearTimeout(timer.current);
        }
      };
      if (videoState.current === 0) {
        player.addEventListener("onStateChange", videoStates, true);
        videoState.current = 1;
      }
      return () => {
        // player.removeEventListener("onStateChange", videoStates,true);
      };
    }
  }, [player, states]);
  // 
  const time = (t) => {
    return (
      Number(t.slice(3, 5)) * 60*1000 +
      Number(t.slice(6, 8)) *1000+
      Number(t.slice(9, 11)) 
    );
  };
  //
  let state;
  const subtitleSearch = (e, sss) => {
    const crttentTime = player.getCurrentTime();
    let mscurtime=Math.round(crttentTime*1000);
    if (e) {
      state = sss.filter((s) => time(s.startTime) > mscurtime)[0];
    } else if (
      sss.filter(
        (s) => time(s.startTime) < mscurtime && time(s.endTime) > mscurtime
      ).length === 0
    ) {
      state = sss.filter((s) => time(s.startTime) > mscurtime)[0];
    } else {
      state = sss.filter(
        (s) => time(s.startTime) < mscurtime && time(s.endTime) > mscurtime
      )[0];
    }
    if (state) {
      setData(state);
      if (time(state.startTime) > mscurtime && !showSubtitle) {
        timer.current = setTimeout(() => {
          setShowSubtitle(true);
        }, (time(state.startTime) - mscurtime));
      } else if (
        time(state.startTime) < mscurtime &&
        !showSubtitle
      ) {
        setShowSubtitle(true);
        timer.current = setTimeout(() => {
          setShowSubtitle(false);
        }, (time(state.endTime) - mscurtime) );
      } else if (showSubtitle) {
        timer.current = setTimeout(() => {
          setShowSubtitle(false);
        }, (time(state.endTime) - mscurtime) );
      } else {
        subtitleSearch(1, sss);
      }
    }
  };
  //
  useEffect(() => {
    if (states) {
      subtitleSearch(0, states);
    }
  }, [showSubtitle]);
  useEffect(() => {
    if (states) {
      subtitleSearch(0, states);
    }
  }, [showSubtitle]);
  //
  useEffect(() => {
    audio2 = document.getElementById("audioPlayer_bak")
    if (audio2) {
      audio2.volume = soundValue / 100
    }
  }, [soundValue])
  useEffect(() => {
    audio = document.getElementById("audioPlayer")
    if (audio) {
      audio.volume = voiceValue / 100
    }
  }, [voiceValue])
  return (
    <Container>
      <Video>
        <YoutubeIframe
          videoId={videoId}
          startTime={0}
          player={player}
          setPlayer={setPlayer}
        />
        <SubtitleContainer show={showSubtitle && !switchThree}>
          <Subtitle show={switchOne}>{data ? data.textTw : ""}</Subtitle>
          <Subtitle2 show={switchTwo}>{data ? data.textMinnan : ""}</Subtitle2>
        </SubtitleContainer>
      </Video>
      <Control>
        <p></p>
        <TestSlider
          value={soundValue}
          sx={{ width: 90, color: "#80c24e", height: 7 }}
          onChange={(e) => setSoundValue(Number(e.target.value))}
        />
        <p></p>
        <TestSlider
          value={voiceValue}
          sx={{ width: 90, color: "#80c24e", height: 7 }}
          onChange={(e) => setVoiceValue(Number(e.target.value))}
        />
        <p></p>
        <SwitchButton
          clicked={() => setSwitchOne(!switchOne)}
          active={switchOne}
        />
        <p></p>
        <SwitchButton
          clicked={() => setSwitchTwo(!switchTwo)}
          active={switchTwo}
        />
        <p></p>
        <SwitchButton
          clicked={() => setSwitchThree(!switchThree)}
          active={switchThree}
          width={43}
          text={1}
          color="#90a3b1"
        />
      </Control>
      <ControlBk />
      <SubtitleContainer2 show={switchThree}>
        <Subtitle show={switchOne && showSubtitle}>{data ? data.textTw : ""}</Subtitle>
        <Subtitle2 show={switchTwo && showSubtitle}>{data ? data.textMinnan : ""}</Subtitle2>
      </SubtitleContainer2>
      <InfoContainer>
        <TextBlock proj={currentProject}></TextBlock>
      </InfoContainer>
      <ButtonsContainer>
        <Button next="/Home" color="999999" title=""></Button>
        <Button next="/Recording" color="e28a2b" title=""></Button>
        {/* <Button color="5397e3" title=""></Button> */}
      </ButtonsContainer>
      <audio id="audioPlayer" style={{ "display": "none" }} controls={true}></audio>
      <audio id="audioPlayer_bak" src={bkMusic} style={{ "display": "none" }} controls={true}></audio>
    </Container>
  );
}

export default Body;
